body {
  font-family: Arial, Helvetica, sans-serif;
}

input {
  margin: 3px;
  border-radius: 3px;
}

button {
  border-radius: 3px;
  background-color: lightgrey;
}

button:hover {
  background-color: lightslategray;
  color: white;
  transition: 0.6s;
}

.content {
  padding: 1em;
  margin: 50px;
  border: 1px dashed black;
  height: auto;
  display: flex;
  flex-direction: row;
}

.error, .success {
  width: 500px;
  text-align: center;
  font-weight: bold;
  transition: 1s;
}

.error {
  color: red;
}

.success {
  color: green;
}

.spot {
  margin-left: auto;
  margin-right: auto;
  align-self: center;
}

.phonebook {
  margin-left: 50px;
}

.names {
  padding: 5px;
}
